.comment
{
  padding: 10px 0;
  border-bottom: 1px solid @color-gray-lighter;

  .clearfix();
  > .avatar,
  > .content
  {
    display: block;
  }

  > .avatar
  {
    width: 40px;
    height: 40px;
    float: left;
    margin-top: 10px;
  }

  > .avatar + .content
  {
    padding-left: 50px;
  }

  > .content
  {
    > .text
    {
      padding-top: 5px;
    }

    > .actions
    {
      > a
      {
        padding-left: 2px;
        display: inline-block;
        padding-right: 5px;
        color: @color-gray;
      }
    }
  }

  &:hover
  {
    > .content
    {
      > .actions
      {
        > a
        {
          color: @link-color;
        }
      }
    }
  }

  > .comments-list
  {
    padding-left: 30px;
    margin-left: 20px;
    border-left: 1px dashed @color-gray-lighter;
    padding-top: 10px;
    padding-bottom: 0;
    > .comment
    {
      border: none;
      border-top: 1px solid @color-gray-lighter;
      &:last-child
      {
        padding-bottom: 0;
      }
    }
  }
}
